import React, { useEffect } from "react";
import { CheckCircle } from "lucide-react";
import "../../styles/dashboard.css";
import { useLanguage } from "../../i18n/LanguageContext";
import { UniversalLink as Link } from "../../database/UniversalLink";
import { useUniversalRouter } from "../../database/routerViteGatsbyUniversal";
import { useUniversalSearchParams } from "../../database/useUniversalSearchParams";

export function CheckoutSuccessPage() {
  const { go } = useUniversalRouter();
  const { t } = useLanguage();
  const [searchParams] = useUniversalSearchParams();
  
  const planId = searchParams.get("plan");

  useEffect(() => {
    const timer = setTimeout(() => {
      go("/app");
    }, 3000);

    return () => clearTimeout(timer);
  }, [go]);

  return (
    <div className="app-container">
      <div className="app-bg-glow"></div>

      <main className="app-main">
        <div className="app-content-column" style={{ maxWidth: "600px", textAlign: "center" }}>
          <div style={{ marginBottom: "48px" }}>
            <CheckCircle 
              size={80} 
              style={{ 
                color: "#10b981", 
                marginBottom: "24px" 
              }} 
            />
            <h1 className="dashboard-title" style={{ color: "#10b981" }}>
              {t("checkout.successTitle")}
            </h1>
            <p style={{ color: "#94a3b8", fontSize: "1.1rem", marginBottom: "32px" }}>
              {t("checkout.successSubtitle")}
            </p>
            
            {planId && (
              <div 
                style={{
                  padding: "16px",
                  borderRadius: "12px",
                  background: "rgba(16, 185, 129, 0.1)",
                  border: "1px solid rgba(16, 185, 129, 0.2)",
                  marginBottom: "32px",
                  color: "#10b981",
                  fontSize: "0.9rem"
                }}
              >
                Plan: {planId}
              </div>
            )}

            <Link to="/app" className="checkout-cta-btn">
              {t("checkout.goToDashboard")}
            </Link>

            <p style={{ 
              color: "#64748b", 
              fontSize: "0.85rem", 
              marginTop: "24px" 
            }}>
              Redirecting automatically in 3 seconds...
            </p>
          </div>
        </div>
      </main>
    </div>
  );
}